.maindiv
{
    /* height: 100%; */
    height: 690px;
    width: 300px;
    /* background-color: #040b14; */
    position: relative;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 9997;
    transition: all 0.5s;
    padding: 0 15px;
    background: #040b14;
    overflow-y: auto;
    box-sizing: border-box;
}
.innermain
{
    display: flex!important;
    flex-direction: column!important;
}
.proimgs
{
    border-radius: 50%!important;
    width: 120px;
    margin: 15px auto;
    display: block;
    border: 8px solid #2c2f3f;
    max-width: 100%;
    height: auto;
}
.profile h1
{
    font-size: 24px;
    margin: 0;
    padding: 0;
    font-weight: 600;
    -moz-text-align-last: center;
    text-align-last: center;
    font-family: "Poppins", sans-serif;
    color: #f8f9fa!important;
}
.social-links
{
    text-align: center!important;
    margin-top: 1rem!important;
}
.profile .social-links a
{
    font-size: 18px;
    display: inline-block;
    background: #212431;
    color: #fff;
    line-height: 1;
    padding: 8px 0;
    margin-right: 10px;
    border-radius: 50%;
    text-align: center;
    width: 36px;
    /* height: 36px; */
    transition: 0.3s;
    text-decoration: none;
    cursor: pointer;
}
.profile .social-links a:hover
{
    background: #149ddd;
    color: #fff;
    text-decoration: none;
}

.sidenav
{
    padding-top: 30px;
}
.sidenav *
{
    margin: 0;
    padding: 0;
    list-style: none;
}
.sidenav > ul > li 
{
    position: relative;
    white-space: nowrap;
}
ul
{
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}
li
{
    display: list-item;
    text-align: -webkit-match-parent;
}
.linkcss
{
    display: flex;
    align-items: center;
    color: #a8a9b4;
    padding: 12px 15px;
    margin-bottom: 8px;
    transition: 0.3s;
    font-size: 15px;
    text-decoration: none;
}
/* .linkcss:hover
{
    color: #fff;
} */
.icons
{
    font-size: 24px;
    padding-right: 8px;
    color: #6f7180;
}
/* .icons:hover
{
    color: #149ddd;
} */
li:hover .linkcss
{
    color: #fff;
}
li:hover .icons
{
    color: #149ddd;
}