.footercss
{
    padding: 15px;
    color: #f4f6fd;
    font-size: 14px;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 300px;
    z-index: 9999;
    background: #040b14;
    box-sizing: border-box;
}
.container
{
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.copyright
{
    text-align: center;
}
.copyright span
{
    font-weight: bolder;
}
.footercss .credits
{
    padding-top: 5px;
    text-align: center;
    font-size: 13px;
    color: #eaebf0;
}
.credits span
{
    color: #149ddd;
    cursor: pointer;
}
*, ::after, ::before
{
    box-sizing: border-box;
}